  --corLinha: #D6DADC;   /*cor da linha da timeline*/
  --espLinha: 4px;         /*espessura da linha da timeline*/
  --espInterno: 40px;      /*espaço interno*/
  --tamSolic: 250px;       /*tamanho do elemento de solicitante*/
}
body {
  background-color: var(--corFundo);
  font-family: Helvetica, sans-serif;
  height: 100%;
